<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>My Videos</p>
        </div>
        <div class="classTypeBox">
            <div class="classTypeItem"  @click="selectVideoType('Created')">
                <p class="classTypeName" :style="selectVideoTypeId == 'Created' ? 'color:#FF30DF;':''">{{'Created ' + creatCount}}</p>
                <span class="classSelectBorder" v-if="selectVideoTypeId == 'Created'"></span>
            </div>
            <div class="classTypeItem"  @click="selectVideoType('Rented')">
                <p class="classTypeName" :style="selectVideoTypeId == 'Rented' ? 'color:#FF30DF;':''">{{'Rented ' + rent}}</p>
                <span class="classSelectBorder" v-if="selectVideoTypeId == 'Rented'"></span>
            </div>
        </div>
        <div class="orderDetailWrap" style="margin-top: .72rem;">
            <van-list
                v-model="creatListLoading"
                :finished="creatfinished"
                loading-text='Loading...'
                @load="getCreatData()"
                v-show="selectVideoTypeId == 'Created'"
            >
                <template v-if="creatListData.length > 0 || !showCreatNoRecord">
                    <div class="whiteItemBox flexBetween" v-for="(item,index) in creatListData" :key="index" @click="goPage('/VideoDetails_created/'+item.id)">
                        <div class="videoLeftBox flexStart">
                            <div class="statusLabel pendingStatus" v-if="item.status == 'pending'">Pending</div>
                            <div class="statusLabel failedStatus" v-else-if="item.status == 'failed'">Failed</div>
                            <div class="statusLabel failedStatus" v-else-if="item.status == 'rejected'">Rejected</div>
                            <div class="statusLabel publishedStatus" v-else-if="item.status == 'published'">Published</div>
                            <div class="statusLabel suspendedStatus" v-else-if="item.status == 'suspended'">Suspended</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'expired'">Expired</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'stopped'">Cancelled</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'cancelled'">Cancelled</div>
                            <div class="statusLabel huiStatus" v-else>{{item.status}}</div>
                            <van-image
                                class='itemImg'
                                :src="item.coverUrl"
                                fit="contain"
                            />
                            <div class="videoMsgBox">
                                <p class="videoName van-ellipsis">{{item.intro}}</p>
                                <p class="videoTime">Posted on {{item.postTime}}</p>
                                <div class="numbersBox flexStart">
                                    <div class="viewsBox" style="width: 1.3rem; margin-right: .24rem;">
                                        <p class="videoNumber">{{item.totalView}}</p>
                                        <p class='numberTitle'>Views</p>
                                    </div>
                                    <div class="viewsBox">
                                        <p class="videoNumber">{{moneySymbol + parseFloat((item.totalEarn/1).toFixed(fixNumber))}}</p>
                                        <p class='numberTitle'>Earned</p>   
                                    </div>
                                </div>
                            </div>
                        </div>
                        <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
                    </div>
                    <div class="finishedBox flexCenter" v-if="creatfinished && creatPage > 2">
                        <div class="huiBorder"></div>
                        <div class="finishedText">Unable to view records from 60 days ago</div>
                    </div>
                </template>
                <template v-else>
                    <div class="noRecordBox flexCenter">
                        <img class='noRecordIcon' src="../assets/images/noRecordIcon.png" alt="">
                        <p class="noRecordText">No record</p>
                    </div>
                </template>
            </van-list>
            <van-list
                v-model="rentedListLoading"
                :finished="rentedfinished"
                loading-text='Loading...'
                @load="getRentedData()"
                v-show="selectVideoTypeId == 'Rented'"
            >
                <template v-if="rentedListData.length > 0 || !showRentalNoRecord">
                    <div class="whiteItemBox flexBetween" v-for="(item,index) in rentedListData" :key="index" @click="goPage('/VideoDetails_rented/'+item.id)">
                        <div class="videoLeftBox flexStart">
                            <div class="statusLabel pendingStatus" v-if="item.status == 'pending'">Pending</div>
                            <div class="statusLabel failedStatus" v-else-if="item.status == 'failed'">Failed</div>
                            <div class="statusLabel failedStatus" v-else-if="item.status == 'rejected'">Rejected</div>
                            <div class="statusLabel publishedStatus" v-else-if="item.status == 'published'">Published</div>
                            <div class="statusLabel suspendedStatus" v-else-if="item.status == 'suspended'">Suspended</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'expired'">Expired</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'stopped'">Cancelled</div>
                            <div class="statusLabel huiStatus" v-else-if="item.status == 'cancelled'">Cancelled</div>
                            <div class="statusLabel huiStatus" v-else>{{item.status}}</div>
                            <van-image
                                class='itemImg'
                                :src="item.coverUrl"
                                fit="contain"
                            />
                            <div class="videoMsgBox">
                                <p class="videoName van-ellipsis">{{item.intro}}</p>
                                <p class="videoTime">Posted on {{item.postTime}}</p>
                                <div class="numbersBox flexStart">
                                    <div class="viewsBox" style="width: 1.21rem; margin-right: .31rem;">
                                        <p class="videoNumber">{{moneySymbol + parseFloat((item.pay/1).toFixed(fixNumber))}}</p>
                                        <p class='numberTitle'>Rental fee</p>
                                    </div>
                                    <div class="viewsBox" style="width: .72rem; margin-right: .45rem;">
                                        <p class="videoNumber">{{item.totalView}}</p>
                                        <p class='numberTitle'>Views</p>
                                    </div>
                                    <div class="viewsBox">
                                        <p class="videoNumber">{{moneySymbol + parseFloat((item.totalEarn/1).toFixed(fixNumber))}}</p>
                                        <p class='numberTitle'>Earned</p>   
                                    </div>
                                </div>
                            </div>
                        </div>
                        <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
                    </div>
                    <div class="finishedBox flexCenter" v-if="rentedfinished  && rentedPage > 2">
                        <div class="huiBorder"></div>
                        <div class="finishedText">Unable to view records from 60 days ago</div>
                    </div>
                </template>
                <template v-else>
                    <div class="noRecordBox flexCenter">
                        <img class='noRecordIcon' src="../assets/images/noRecordIcon.png" alt="">
