                    </div>
                </template>
            </van-list>
        </div>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'MyVideos',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                fixNumber: https.fixNumber,
                selectVideoTypeId: 'Created',

                listType: 'creat',
                creatCount: '',
                rent: '',

                creatListData: [],
                creatListLoading: false,
                creatfinished: false,
                creatPage: 1,
                showCreatNoRecord: false,

                rentedListData: [],
                rentedListLoading: false,
                rentedfinished: false,
                rentedPage: 1,
                showRentalNoRecord: false,
                
			}
		},
		created(){
			// this.getData()
            this.getRentedData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getCreatData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    page: this.creatPage
                }
				https.fetchGet('/api/video/creation/list',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.creatCount = res.data.creation
                        this.rent = res.data.rent
                        this.creatListData = this.creatListData.concat(res.data.list)
                        this.creatListLoading = false
                        this.creatPage++
                        if(!res.data.hasNext) {
                            this.creatfinished = true
                            console.log('finished')
                        }
                        if(this.creatListData.length == 0) {
                            this.showCreatNoRecord = true
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getCreatData()
					}
				})
			},
            getRentedData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    page: this.rentedPage
                }
				https.fetchGet('/api/video/rent/list',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.creatCount = res.data.creation
                        this.rent = res.data.rent
                        this.rentedListData = this.rentedListData.concat(res.data.list)
                        this.rentedListLoading = false
                        this.rentedPage++
                        if(!res.data.hasNext) {
                            this.rentedfinished = true
                            console.log('finished')
                        }
                        if(this.rentedListData.length == 0) {
                            this.showRentalNoRecord = true
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getCreatData()
					}
				})
			},
            selectVideoType(item){
                // this.creatListLoading = false,
                // this.creatfinished = false,
                // this.creatPage = 1,

                // this.rentedListLoading = false,
                // this.rentedfinished = false,
                // this.rentedPage = 1,

                this.selectVideoTypeId = item
                // if(item == 'Created') {
                //     this.getCreatData()
                // }else {
                //     this.getRentedData()
                // }
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
